import { Container, Row, Col, Form, Button } from 'react-bootstrap';
import Select from 'react-select';
import 'bootstrap-icons/font/bootstrap-icons.css';

import { useState, useEffect } from 'react';
import axios from 'axios';
import GetCookie from '../../auth/hooks/GetCookie';
import { useNavigate } from 'react-router-dom';
import AdminNavbar from '../../navbar/AdminNavbar';

export default function AddData() {
    const Navigate = useNavigate();

    const [Nama, setNama] = useState('');
    const [JudulPenelitian, setJudulPenelitian] = useState('');
    const [Hari, setHari] = useState('');
    const [Tanggal, setTanggal] = useState('');
    const [Jam, setJam] = useState('');
    const [Ruang, setRuang] = useState('');

    const [isNull, setIsNull] = useState(false);
    const [Message, setMessage] = useState('');
    const [Status, setStatus] = useState(false);

    const [Username, setUsername] = useState('');
    const [Role, setRole] = useState('');

    const hari = [
        { value: 'Senin', label: 'Senin' },
        { value: 'Selasa', label: 'Selasa' },
        { value: 'Rabu', label: 'Rabu' },
        { value: 'Kamis', label: 'Kamis' },
        { value: 'Jumat', label: 'Jumat' },
        { value: 'Sabtu', label: 'Sabtu' },
        { value: 'Minggu', label: 'Minggu' }
    ];

    const displayData = () => {
        return (
            <Col lg={12} className='mt-4 text-center text-light bg-warning p-4 rounded-3'>
                <h5 className='my-auto'>Harap isi semua data</h5>
            </Col>
        );
    }

    const displaySucces = () => {
        return (
            <Col lg={12} className='mt-4 text-center text-light bg-success p-4 rounded-3'>
                <h5 className='my-auto'>{Message}</h5>
            </Col>
        );
    }

    const checkData = async (e) => {
        e.preventDefault();
        if (Nama === '' || JudulPenelitian === '' || Hari === '' || Tanggal === '' || Jam === '' || Ruang === '') {
            setIsNull(true);
        }
        else {
            setIsNull(false);
            try {
                const Response = await axios.post('http://139.59.111.182/ws/backend/api.php?api=addData',
                    { nama: Nama, judul_penelitian: JudulPenelitian, hari: Hari, tanggal: Tanggal, jam: Jam, ruang: Ruang },
                    { headers: { 'Content-Type': 'application/json' }, withCredentials: false });

                if (Response.data.status === 1) {
                    setMessage("Data berhasil ditambahkan");
                    setStatus(true);
                    setNama(''); setJudulPenelitian(''); setHari(''); setTanggal(''); setJam(''); setRuang('');
                }
                else { setMessage("Data Gagal Ditambahkan"); }
            } catch (error) {
                console.log(error)
            }
        }
    }

    const isLogin = async () => {
        try {
            const Response = await axios.post('http://139.59.111.182/ws/backend/api.php?api=isLogin',
                { token: GetCookie('__token__') })

            if (Response.data.status === 1) {
                setUsername(Response.data.username);
                setRole(Response.data.role);
            }

            else { Navigate('/'); }
        } catch (error) {
            console.log(error);
        }
    }

    useEffect(() => {
        isLogin();

        let timer;
        if (Status) {
            timer = setTimeout(() => setStatus(false), 1000);
            timer = setTimeout(() => window.location.reload(), 1000);
        }

        return () => {
            clearTimeout(timer);
        };
    }, [Status]);

    return (
        <>
            <AdminNavbar />
            <Container>
                <Row className='mb-5'>
                    {isNull ? displayData() : null}
                    {Status ? displaySucces() : null}
                    <Col lg={12} className='p-3 bg-light rounded-3 mt-4'>
                        <div className='d-flex justify-content-between align-items-center px-4'>
                            <h3 className='my-auto fw-bold'>Input Data ke Database</h3>
                            <i className='bi bi-plus-square-fill my-auto' style={{ fontSize: '2rem' }} />
                        </div>
                    </Col>
                    <Col lg={12} className='bg-light mt-4 rounded-3 mb-5'>
                        <Col lg={12} className='p-4 rounded-3 mt-4'>
                            <Form onSubmit={checkData}>
                                <Form.Group as={Row} className='mb-3'>
                                    <Form.Label column sm='2' className='my-auto text-dark fw-bold'>Judul Penelitian</Form.Label>
                                    <Col sm='10'>
                                        <Form.Control className='p-3' value={JudulPenelitian} type='text' placeholder='Judul Penelitian' required onChange={(e) => setJudulPenelitian(e.target.value)} />
                                    </Col>
                                </Form.Group>
                                <Form.Group as={Row} className='mb-3'>
                                    <Form.Label column sm='2' className='my-auto text-dark fw-bold'>Nama</Form.Label>
                                    <Col sm='10'>
                                        <Form.Control className='p-3' value={Nama} type='text' placeholder='Nama' required onChange={(e) => setNama(e.target.value)} />
                                    </Col>
                                </Form.Group>
                                <Form.Group as={Row} className='mb-3'>
                                    <Form.Label column sm='2' className='my-auto text-dark fw-bold'>Hari</Form.Label>
                                    <Col sm='10'>
                                        <Select options={hari} placeholder='Hari' required onChange={(e) => setHari(e.value)} />
                                    </Col>
                                </Form.Group>
                                <Form.Group as={Row} className='mb-3'>
                                    <Form.Label column sm='2' className='my-auto text-dark fw-bold'>Tanggal</Form.Label>
                                    <Col sm='10'>
                                        <Form.Control className='p-3' value={Tanggal} type='date' onChange={(e) => setTanggal(e.target.value)} required />
                                    </Col>
                                </Form.Group>
                                <Form.Group as={Row} className='mb-3'>
                                    <Form.Label column sm='2' className='my-auto text-dark fw-bold'>Jam</Form.Label>
                                    <Col sm='10'>
                                        <Form.Control className='p-3' value={Jam} type='time' onChange={(e) => setJam(e.target.value)} required />
                                    </Col>
                                </Form.Group>
                                <Form.Group as={Row} className='mb-5'>
                                    <Form.Label column sm='2' className='my-auto text-dark fw-bold'>Ruangan</Form.Label>
                                    <Col sm='10'>
                                        <Form.Control className='p-3' value={Ruang} type='text' placeholder='Input Ruangan' required onChange={(e) => setRuang(e.target.value)} />
                                    </Col>
                                </Form.Group>
                                <Form.Group as={Row} className='mb-3'>
                                    <Col sm='6' className='mb-3'>
                                        <Button variant='primary' type='submit' className='w-100 p-3' onClick={checkData}>Submit</Button>
                                    </Col>
                                    <Col sm='6'>
                                        <Button variant='outline-secondary' type='reset' className='w-100 p-3'>Cancel</Button>
                                    </Col>
                                </Form.Group>
                            </Form>
                        </Col>
                    </Col>
                </Row>
            </Container>
        </>
    )
}