import { Container , Row , Col , Form , Button } from 'react-bootstrap';
import 'bootstrap-icons/font/bootstrap-icons.css';

import axios from 'axios';

import { useState } from 'react';

import SetCookie from '../hooks/SetCookie';
import RemoveCookie from '../hooks/RemoveCookie';
import { useNavigate } from 'react-router-dom';
import NavigationBar from '../../navbar/NavigationBar';

export default function Login(){
    const Navigate = useNavigate();

    const[Username, setUsername] = useState('');
    const[Password , setPassword] = useState('');

    const[Message , setMessage] = useState(false);

    const Login = async(e) => {
        e.preventDefault();
        try {
            const Response = await axios.post('http://139.59.111.182/ws/backend/api.php?api=Login',
            {username : Username , password : Password});
            
            if(Response.data.status == 1){
                setMessage(false);

                RemoveCookie('__token__');
                SetCookie('__token__' , Response.data.token);

                Navigate('/Dashboard');
            }
            else{
                setMessage(true);
            }

        } catch (error) {
            console.log(error);
        }
    }

    const displayWarning = () => {
        return(
            <Row>
                <Col lg = {6} className = 'mb-3 m-auto bg-warning'>
                    <div className = 'd-flex justify-content-center text-center bg-warning px-5 py-3 text-light'>
                        <h5 className='my-auto text-center'>Username atau password salah</h5>
                    </div>
                </Col>
            </Row>
        )
    }

    return(
        <>
            <NavigationBar />
            <Container className = 'px-4 my-5'>
                {Message ? displayWarning() : null}
                <Row className = 'mb-5'>
                    <Col lg = {6} className = 'm-auto mb-5 bg-light p-0'>
                        <div className = 'd-flex justify-content-between vertical-align-center bg-primary px-5 py-3 text-light'>
                            <h2 className='my-auto'>Login Page</h2>
                            <i className='bi bi-arrow-right-circle my-auto' style = {{fontSize : '1.7rem'}} />
                        </div>
                        <Col lg = {12} className = 'p-5'>
                            <Form onSubmit = {Login}> 
                                <Form.Group as = { Row } className = 'mb-3'>
                                    <Form.Label column sm = '12' className = 'my-auto text-dark fw-bold'>Username</Form.Label>
                                    <Col sm = '12'>
                                        <Form.Control className = 'px-4 p-3'type = 'text' placeholder = 'Username' required onChange = {(e) => setUsername(e.target.value)} />
                                    </Col>
                                </Form.Group>
                                <Form.Group as = { Row } className = 'mb-5'>
                                    <Form.Label column sm = '12' className = 'my-auto text-dark fw-bold'>Password</Form.Label>
                                    <Col sm = '12'>
                                        <Form.Control className = 'px-4 p-3' type = 'password' placeholder = 'Password' required onChange = {(e) => setPassword(e.target.value)} />
                                    </Col>
                                </Form.Group>
                                <Form.Group as = { Row }>
                                    <Col sm = '12'>
                                        <Button variant = 'primary' type = 'submit' className = 'w-100 p-3 fs-5' onClick = {Login}>Login</Button>
                                    </Col>
                                </Form.Group>
                            </Form>
                        </Col>
                    </Col>
                </Row>
                
            </Container>
        </>
    )
}