import { Container, Row, Col, Button } from 'react-bootstrap';
import AdminNavbar from '../../navbar/AdminNavbar';

import 'bootstrap-icons/font/bootstrap-icons.css';

import GetCookie from '../../auth/hooks/GetCookie';

import { useState, useEffect } from 'react';
import { useNavigate } from 'react-router-dom';

import axios from 'axios';

export default function Dashboard() {
    const Navigate = useNavigate();

    const [Username, setUsername] = useState('');

    const isLogin = async () => {
        try {
            const Response = await axios.post('https://searching.tudemaha.my.id/backend/api.php?api=isLogin',
                { token: GetCookie('__token__') })

            if (Response.data.status == 1) {
                setUsername(Response.data.username);
            }

            else { Navigate('/'); }
        } catch (error) {
            console.log(error);
        }
    }

    useEffect(() => {
        isLogin();
    }, [])


    return (
        <>
            <AdminNavbar />

            <Container className='mb-5'>
                <Row className='mt-5 mb-5'>
                    <Col lg={4} className='mb-4'>
                        <Col lg={12} className='bg-light rounded-3 p-5 text-center' >
                            <h2>Profile Page</h2>
                            <i className=" bi bi-person-circle" style={{ fontSize: "10rem" }} />
                            <h5>Welcome, <b>{Username}</b></h5>

                            <Button className='w-100 p-3 mt-5' onClick={() => Navigate('/Logout')}>Logout</Button>
                        </Col>
                    </Col>
                    <Col lg={7} className='mx-auto'>
                        <Col lg={12} className='p-3 bg-light rounded-3' onClick={() => Navigate('/AddData')}>
                            <div className='d-flex justify-content-between align-items-center px-4'>
                                <h3 className='my-auto fw-bold'>Input Data ke Database</h3>
                                <i className='bi bi-plus-square-fill my-auto' style={{ fontSize: '2rem' }} />
                            </div>
                        </Col>
                        <Col lg={12} className='p-3 bg-light rounded-3 mt-3' onClick={() => Navigate('/Update')}>
                            <div className='d-flex justify-content-between align-items-center px-4'>
                                <h3 className='my-auto fw-bold'>Update Data pada Database</h3>
                                <i className='bi bi-file-earmark-spreadsheet-fill my-auto' style={{ fontSize: '2rem' }} />
                            </div>
                        </Col>
                        <Col lg={12} className='p-3 bg-light rounded-3 mt-3' onClick={() => Navigate('/Searching')}>
                            <div className='d-flex justify-content-between align-items-center px-4'>
                                <h3 className='my-auto fw-bold'>Search Data pada Database</h3>
                                <i className='bi bi-search my-auto' style={{ fontSize: '2rem' }} />
                            </div>
                        </Col>
                        <Col lg={12} className='p-3 bg-light rounded-3 mt-3 mb-5' onClick={() => Navigate('/Delete')}>
                            <div className='d-flex justify-content-between align-items-center px-4'>
                                <h3 className='my-auto fw-bold'>Delete Data pada Database</h3>
                                <i className='bi bi-trash-fill my-auto' style={{ fontSize: '2rem' }} />
                            </div>
                        </Col>
                    </Col>
                </Row>
            </Container>
        </>
    )
}