import { Container , Row , Col, Button, Form, Modal } from 'react-bootstrap';
import AdminNavbar from '../../navbar/AdminNavbar';
import Select from 'react-select';

import 'bootstrap-icons/font/bootstrap-icons.css';

import GetCookie from '../../auth/hooks/GetCookie';
import { useState, useEffect } from 'react';
import { useNavigate } from 'react-router-dom';

import axios from 'axios';

export default function Delete(){
    const Navigate = useNavigate();

    const[Id , setId] = useState('');
    const[Data, setData] = useState([]);

    const[Judul , setJudul] = useState('');
    const[Nama , setNama] = useState('');

    const[Show , setShow] = useState(false);

    const capitalizeFirst = str => {
        return str.charAt(0).toUpperCase() + str.slice(1);
    };

    const changeFormatYears = str => {
        const[year , month , day] = str.split('-');
        return `${day}-${month}-${year}`; 
    }

    const isLogin = async() =>{
        try {
            const Response = await axios.post('http://139.59.111.182/ws/backend/api.php?api=isLogin' , 
            {token : GetCookie('__token__')})
            
            if(Response.data.status == 1){ 
                
            }
            
            else{ Navigate('/'); }
        } catch (error) {
            console.log(error);
        }
    }
    const getData = async() =>{
        try {
            const Response = await axios.get('http://139.59.111.182/ws/backend/api.php?api=getData');
            setData(Response.data);
        } catch (error) {
            console.log(error);
        }
    }

    const showData = async(ID) =>{
        try {
            const Response = await axios.post('http://139.59.111.182/ws/backend/api.php?api=showData' , 
            {id : ID});

            Response.data.map((item) => {
                setId(item.id)
                setNama(item.nama);
                setJudul(item.judul_penelitian);
            });
            setShow(true);
            
        } catch (error) {
            console.log(error);
        }
    }

    const Deleted = async(e) => {
        e.preventDefault();
        try {
            const Response = await axios.post('http://139.59.111.182/ws/backend/api.php?api=deleteData' , 
            {id : Id});

            getData();

        } catch (error) {
            console.log(error);
        }
        setShow(false);
    }

    useEffect(() =>{
        isLogin();
        getData();
    }, [])

    return(
        <>
            <AdminNavbar />

            <Modal show = {Show} onHide = {() => setShow(false)} fullscreen='sm-down' centered>
                <Modal.Header closeButton>
                    <Modal.Title>Delete Data</Modal.Title>
                </Modal.Header>
                <Modal.Body>
                    <h5>Kamu Yakin Akan Menghapus Data dengan: </h5>
                    <p> Judul: <b>{Judul}</b> <br />
                        Nama: <b>{Nama}</b>
                    </p>
                </Modal.Body>
                <Modal.Footer>
                    <Button className = 'btn-danger px-4 py-2' onClick = {Deleted}>Hapus</Button>
                    <Button variant = 'outline-secondary' className = 'px-4 py-2' onClick = {() => setShow(false)}>Cancel</Button>
                </Modal.Footer>
            </Modal>

            <Container className = 'mt-5 mb-5'>
                <Row>
                    <Col lg = {12} className = 'bg-light mb-4'>
                        <div className = 'd-flex justify-content-between p-3'>
                            <h3 className='my-auto'>Update Data pada Database</h3>
                        </div>
                    </Col>
                </Row>
                
                
                {
                    Data.map((item) =>
                        <Row key={item.id}>
                            <Col lg = {12} className = 'bg-primary text-light mb-1' >
                                <h5 className = 'm-auto p-4'>
                                    Jadwal Deseminasi: 
                                    {' ' + capitalizeFirst(item.hari) + ', ' + changeFormatYears(item.tanggal)}
                                </h5>
                            </Col>
                            <Col md = {12} className = 'bg-light text-dark'>
                                <div className = 'm-auto py-4 result-data'> 
                                    <Row>
                                        <Col md = {1} className = 'result-data'>
                                            <h5>Hari</h5>
                                            <Col lg = {12}>
                                                { capitalizeFirst(item.hari) }
                                            </Col>
                                        </Col>

                                        <Col md = {2} className = 'result-data'>
                                            <h5>Tanggal</h5>
                                            <Col lg = {12}>
                                                { changeFormatYears(item.tanggal) }
                                            </Col>
                                        </Col>

                                        <Col md = {1} className = 'result-data'>
                                            <h5>Jam</h5>
                                            <Col lg = {12}>
                                                { item.jam }
                                            </Col>
                                        </Col>

                                        <Col md = {3} className = 'result-data'>
                                            <h5>Nama</h5>
                                            <Col lg = {12}>
                                                { item.nama }
                                            </Col>
                                        </Col>

                                        <Col md = {3} className = 'result-data'>
                                            <h5>Judul</h5>
                                            <Col lg = {12}>
                                                { item.judul_penelitian }
                                            </Col>
                                        </Col>

                                        <Col md = {2} className = 'result-data'>
                                            <h5>Ruang</h5>
                                            <Col lg = {12}>
                                                { item.ruang }
                                            </Col>
                                        </Col>
                                    </Row>
                                </div>
                                
                            </Col>
                            <Col md = {12} className = 'bg-danger mb-5 text-light text-center p-3' onClick = {() => showData(item.id)}>
                                <h5 className = 'my-auto'>Delete Data ini</h5>
                            </Col>
                        </Row>
                    )
                }
            </Container>
        </>
    )
}