import { Row , Col , Form , Container } from 'react-bootstrap';
import { useState } from 'react';
import axios from 'axios';
import Image from '../../../img/study.webp';
import 'bootstrap-icons/font/bootstrap-icons.css';

import '../../searching/style/Searching.css';
import AdminNavbar from '../../navbar/AdminNavbar';

export default function Searching(){
    const[searchJudul , setSearchJudul] = useState('');
    const[searchNama , setSearchNama] = useState('');

    const[Data , setData] = useState([]);
    const[Message , setMessage] = useState('');

    const searchData = async (e) => {
        if(searchJudul !== '' || searchNama !== ''){
            e.preventDefault();
            try {
                const Response = await axios.post('http://139.59.111.182/ws/backend/api.php?api=searchData', 
                { judul_penelitian : searchJudul , nama : searchNama } );

                if(Response.data.status === 0){ setMessage(Response.data.message); setData([]); }
                else{ setData(Response.data); setMessage(''); }

            } catch (error) {
                console.log(error);
                setMessage(error)
            }
        }
    }

    const capitalizeFirst = str => {
        return str.charAt(0).toUpperCase() + str.slice(1);
    };

    const changeFormatYears = str => {
        const[year , month , day] = str.split('-');
        return `${day}-${month}-${year}`; 
    }

    const displayData = () =>{
        return(
            <div className = 'data-show-search'>
                {
                    Data.map((item) =>
                        <Row key={item.id}>
                            <Col lg = {12} className = 'bg-primary text-light mb-1' >
                                <h5 className = 'm-auto p-4'>
                                    Jadwal Deseminasi: 
                                    {' ' + capitalizeFirst(item.hari) + ', ' + changeFormatYears(item.tanggal)}
                                </h5>
                            </Col>
                            <Col md = {12} className = 'bg-light text-dark mb-5'>
                                <div className = 'm-auto py-4 result-data'> 
                                    <Row>
                                        <Col md = {1} className = 'result-data'>
                                            <h5>Hari</h5>
                                            <Col lg = {12}>
                                                { capitalizeFirst(item.hari) }
                                            </Col>
                                        </Col>

                                        <Col md = {2} className = 'result-data'>
                                            <h5>Tanggal</h5>
                                            <Col lg = {12}>
                                                { changeFormatYears(item.tanggal) }
                                            </Col>
                                        </Col>

                                        <Col md = {1} className = 'result-data'>
                                            <h5>Jam</h5>
                                            <Col lg = {12}>
                                                { item.jam }
                                            </Col>
                                        </Col>

                                        <Col md = {3} className = 'result-data'>
                                            <h5>Nama</h5>
                                            <Col lg = {12}>
                                                { item.nama }
                                            </Col>
                                        </Col>

                                        <Col md = {3} className = 'result-data'>
                                            <h5>Judul</h5>
                                            <Col lg = {12}>
                                                { item.judul_penelitian }
                                            </Col>
                                        </Col>

                                        <Col md = {2} className = 'result-data'>
                                            <h5>Ruang</h5>
                                            <Col lg = {12}>
                                                { item.ruang }
                                            </Col>
                                        </Col>
                                    </Row>
                                </div>
                            </Col>
                        </Row>
                    )
                }
                
            </div>
        )
    }
    
    return(
        <div className = 'searching'>
            <AdminNavbar />
            <Container>
                <Row className = 'mb-4 vertical-center'>
                    <Col lg = {5} className = 'mx-auto p-4 text-center'>
                        <img src = {Image} width = '100%' alt = "search" />
                    </Col>
                    <Col lg = {6} className = 'p-4 m-auto bg-light rounded-3'>
                        {Message ? 
                            <div className = 'd-flex justify-content-center align-item-center bg-warning p-2 mb-4 rounded-3 text-light'>
                                { capitalizeFirst(Message) }
                            </div>
                            : null
                        }
                
                        <Form onSubmit = { searchData }>
                            <div className = 'd-flex justify-content-between align-items-center'>
                                <h1 className = 'mb-4 text-dark fw-bold'>Search Data</h1>
                                <i className = 'bi bi-search my-auto' style = {{ fontSize : '2rem' }} />
                            </div>
                            <Form.Group controlId = "formBasicJudul" className = 'mb-3'>
                                <Form.Label>Judul Penelitian</Form.Label>
                                <Form.Control type = "text" placeholder = "Masukkan Judul" className = 'px-3 py-3' onChange = {(e) => setSearchJudul(e.target.value)} />
                            </Form.Group>
                            <Form.Group controlId = "formBasicNama" className = 'mb-3'>
                                <Form.Label>Nama</Form.Label>
                                <Form.Control type = "text" placeholder = "Masukkan Nama" className = 'px-3 py-3' onChange = {(e) => setSearchNama(e.target.value)} />
                            </Form.Group>
                            <Form.Group controlId = "formBasicButton">
                                <Form.Control type = "submit" value = "Cari" className = 'btn btn-primary p-3' onClick = {searchData} />
                            </Form.Group>
                        </Form>
                    </Col>
                </Row>
                
                { Data !== '' ? displayData() : null }
                
            </Container>
        </div>
    )
}