import React from 'react';
import { CDBFooter, CDBBtn, CDBIcon, CDBBox } from 'cdbreact';
import './style/Footer.css';

export default function Footer() {
  return (
    <CDBFooter className="footer fixed-bottom bg-white">
      <CDBBox
        display="flex"
        justifyContent="center"
        alignItems="center"
        className="mx-auto py-4 flex-wrap"
        style={{ width: '80%' }}
      >

        <CDBBox>
          <p className='my-auto'> &copy; Tim Peneliti Informatika 2022 </p>
        </CDBBox>
        
      </CDBBox>
    </CDBFooter>
  );
};