import Container from 'react-bootstrap/Container';
import Nav from 'react-bootstrap/Nav';
import Navbar from 'react-bootstrap/Navbar';
import Offcanvas from 'react-bootstrap/Offcanvas';
import './style/NavigationBar.css';

import Logo from '../../img/logo.png';

export default function NavigationBar() {
    const expand = 'lg';

    return (
        <>
            <Navbar key = {expand} expand = {expand} className = "Navbar p-3" id = "sticky-nav" bg='light' sticky='top'>
                <Container>
                    <Navbar.Toggle aria-controls = "offcanvasNavbar-expand-lg" className = "Nav-Toggle" />
                    <Navbar.Brand href="/" className = "Brand" > <img src={Logo} alt="Logo" className = "Logo" width = '80%' /> </Navbar.Brand>
                    
                    <Navbar.Offcanvas id={`offcanvasNavbar-expand-${expand}`} aria-labelledby={`offcanvasNavbarLabel-expand-${expand}`} placement="end">
                        <Offcanvas.Header closeButton className = "Offcanvas-header">
                            <Offcanvas.Title id={`offcanvasNavbarLabel-expand-${expand}`}>
                                <img src={Logo} alt="Logo" className = "Logo" width = '80%' />
                            </Offcanvas.Title>
                        </Offcanvas.Header>
                        <Offcanvas.Body className = "Offcanvas-body">
                            <Nav className="justify-content-end flex-grow-1 pe-3 Nav-inside">
                                <Nav.Link className = "Nav-Link" href = '/'> Home </Nav.Link>
                                <Nav.Link className = "Nav-Link" href = '/Searching'> Search Data </Nav.Link>
                                <Nav.Link className = "Nav-Link" href = '/Login'> Login </Nav.Link>
                            </Nav>
                        </Offcanvas.Body>
                    </Navbar.Offcanvas>
                </Container>
            </Navbar>
        </>
  );
}