import { Container , Row , Col, Button, Form, Modal } from 'react-bootstrap';
import AdminNavbar from '../../navbar/AdminNavbar';
import Select from 'react-select';

import 'bootstrap-icons/font/bootstrap-icons.css';

import GetCookie from '../../auth/hooks/GetCookie';
import { useState, useEffect } from 'react';
import { useNavigate } from 'react-router-dom';

import axios from 'axios';

export default function Update(){
    const Navigate = useNavigate();

    const[Id , setId] = useState('');
    const[Data, setData] = useState([]);
    const[Show , setShow] = useState(false);

    const[Nama , setNama] = useState('');
    const[JudulPenelitian , setJudulPenelitian] = useState('');
    const[Hari , setHari] = useState('');
    const[Tanggal , setTanggal] = useState('');
    const[Jam , setJam] = useState('');
    const[Ruang , setRuang] = useState('');

    const hari = [
        { value: 'Senin' , label: 'Senin' },
        { value: 'Selasa' , label: 'Selasa' },
        { value: 'Rabu' , label: 'Rabu' },
        { value: 'Kamis' , label: 'Kamis' },
        { value: 'Jumat' , label: 'Jumat' },
        { value: 'Sabtu' , label: 'Sabtu' },
        { value: 'Minggu' , label: 'Minggu' }
    ];

    const capitalizeFirst = str => {
        return str.charAt(0).toUpperCase() + str.slice(1);
    };

    const changeFormatYears = str => {
        const[year , month , day] = str.split('-');
        return `${day}-${month}-${year}`; 
    }

    const isLogin = async() =>{
        try {
            const Response = await axios.post('http://139.59.111.182/ws/backend/api.php?api=isLogin' , 
            {token : GetCookie('__token__')})
            
            if(Response.data.status == 1){ 
                
            }
            
            else{ Navigate('/'); }
        } catch (error) {
            console.log(error);
        }
    }

    const getData = async() =>{
        try {
            const Response = await axios.get('http://139.59.111.182/ws/backend/api.php?api=getData');
            setData(Response.data);
        } catch (error) {
            console.log(error);
        }
    }

    const showData = async(ID) => {
        try {
            const Response = await axios.post('http://139.59.111.182/ws/backend/api.php?api=showData' , 
            {id : ID});

            Response.data.map((item) => {
                setId(item.id)
                setNama(item.nama);
                setJudulPenelitian(item.judul_penelitian);
                setTanggal(item.tanggal);
                setJam(item.jam);
                setRuang(item.ruang);
            });
            
        } catch (error) {
            console.log(error);
        }
        setShow(true);
    }

    const Updated = async(e) => {
        e.preventDefault();
        try {
            const Response = await axios.post('http://139.59.111.182/api.php?api=UpdateData' , 
            {id : Id , nama : Nama , judul_penelitian : JudulPenelitian , hari : Hari , tanggal : Tanggal , jam : Jam , ruang : Ruang});

            if(Response.data.status == 1){
                setShow(false);
                getData();
            }
        } catch (error) {
            console.log(error);
        }
    }

    useEffect(() =>{
        isLogin();
        getData();

    }, [])

    return(
        <>
            <AdminNavbar />

            <Modal show = {Show} onHide = {() => setShow(false)} fullscreen='sm-down' centered>
                <Modal.Header closeButton>
                    <Modal.Title>Update Data</Modal.Title>
                </Modal.Header>
                <Modal.Body>
                    <Form onSubmit = {Updated}>
                    <Form.Group as = { Row } className = 'mb-3'>
                        <Form.Label column sm = '2' className = 'my-auto text-dark fw-bold'>Judul Penelitian</Form.Label>
                        <Col sm = '10'>
                            <Form.Control className = 'p-3' value = {JudulPenelitian} type = 'text' placeholder = 'Judul Penelitian' required onChange = {(e) => setJudulPenelitian(e.target.value)} />
                        </Col>
                    </Form.Group>
                        <Form.Group as = { Row } className = 'mb-3'>
                            <Form.Label column sm = '2' className = 'my-auto text-dark fw-bold'>Nama</Form.Label>
                            <Col sm = '10'>
                                <Form.Control className = 'p-3' value = {Nama} type = 'text' placeholder = 'Nama' required onChange = {(e) => setNama(e.target.value)} />
                            </Col>
                        </Form.Group>
                        <Form.Group as = { Row } className = 'mb-3'>
                            <Form.Label column sm = '2' className = 'my-auto text-dark fw-bold'>Hari</Form.Label>
                            <Col sm = '10'>
                                <Select options = { hari } placeholder = 'Hari' required onChange = {(e) => setHari(e.value)} />
                            </Col>
                        </Form.Group>
                        <Form.Group as = { Row } className = 'mb-3'>
                            <Form.Label column sm = '2' className = 'my-auto text-dark fw-bold'>Tanggal</Form.Label>
                            <Col sm = '10'>
                                <Form.Control className = 'p-3' value = {Tanggal} type = 'date' onChange = {(e) => setTanggal(e.target.value)} required />
                            </Col>
                        </Form.Group>
                        <Form.Group as = { Row } className = 'mb-3'>
                            <Form.Label column sm = '2' className = 'my-auto text-dark fw-bold'>Jam</Form.Label>
                            <Col sm = '10'>
                                <Form.Control className = 'p-3' value = {Jam} type = 'time' onChange = {(e) => setJam(e.target.value)} required />
                            </Col>
                        </Form.Group>
                        <Form.Group as = { Row } className = 'mb-5'>
                            <Form.Label column sm = '2' className = 'my-auto text-dark fw-bold'>Ruangan</Form.Label>
                            <Col sm = '10'>
                                <Form.Control className = 'p-3' value = {Ruang} type = 'text' placeholder = 'Input Ruangan' required onChange = {(e) => setRuang(e.target.value)} />
                            </Col>
                        </Form.Group>
                        <Form.Group as = { Row } className = 'mb-3'>
                            <Col sm = '6' className = 'mb-3'>
                                <Button variant = 'primary' type = 'submit' className = 'w-100 p-3' onClick={Updated}>Submit</Button>
                            </Col>
                            <Col sm = '6'>
                                <Button variant = 'outline-secondary' type = 'reset' className = 'w-100 p-3' onClick={() => setShow(false)}>Cancel</Button>
                            </Col>
                        </Form.Group>
                    </Form>
                </Modal.Body>
            </Modal>


            <Container className = 'mt-5 mb-5'>
                <Row>
                    <Col lg = {12} className = 'bg-light mb-4'>
                        <div className = 'd-flex justify-content-between p-3'>
                            <h3 className='my-auto'>Update Data pada Database</h3>
                        </div>
                    </Col>
                </Row>
                
                
                {
                    Data.map((item) =>
                        <Row key={item.id}>
                            <Col lg = {12} className = 'bg-primary text-light mb-1' >
                                <h5 className = 'm-auto p-4'>
                                    Jadwal Deseminasi: 
                                    {' ' + capitalizeFirst(item.hari) + ', ' + changeFormatYears(item.tanggal)}
                                </h5>
                            </Col>
                            <Col md = {12} className = 'bg-light text-dark'>
                                <div className = 'm-auto py-4 result-data'> 
                                    <Row>
                                        <Col md = {1} className = 'result-data'>
                                            <h5>Hari</h5>
                                            <Col lg = {12}>
                                                { capitalizeFirst(item.hari) }
                                            </Col>
                                        </Col>

                                        <Col md = {2} className = 'result-data'>
                                            <h5>Tanggal</h5>
                                            <Col lg = {12}>
                                                { changeFormatYears(item.tanggal) }
                                            </Col>
                                        </Col>

                                        <Col md = {1} className = 'result-data'>
                                            <h5>Jam</h5>
                                            <Col lg = {12}>
                                                { item.jam }
                                            </Col>
                                        </Col>

                                        <Col md = {3} className = 'result-data'>
                                            <h5>Nama</h5>
                                            <Col lg = {12}>
                                                { item.nama }
                                            </Col>
                                        </Col>

                                        <Col md = {3} className = 'result-data'>
                                            <h5>Judul</h5>
                                            <Col lg = {12}>
                                                { item.judul_penelitian }
                                            </Col>
                                        </Col>

                                        <Col md = {2} className = 'result-data'>
                                            <h5>Ruang</h5>
                                            <Col lg = {12}>
                                                { item.ruang }
                                            </Col>
                                        </Col>
                                    </Row>
                                </div>
                                
                            </Col>
                            <Col md = {12} className = 'bg-success mb-5 text-light text-center p-3' onClick = {() => showData(item.id)}>
                                <h5 className = 'my-auto'>Update Data ini</h5>
                            </Col>
                        </Row>
                    )
                }
            </Container>
        </>
    )
}